"use client"

import { ScrollArea } from "@/components/ui/scroll-area"
import { Button } from "@/components/ui/button"
import { Hash, Volume2, PlusCircle, ChevronDown } from "lucide-react"
import { useServer } from "@/contexts/ServerContext"
import { UserMenu } from "@/components/user/UserMenu"

interface Channel {
  id: string
  name: string
  type: "text" | "voice"
}

const mockChannels: Channel[] = [
  { id: "1", name: "general", type: "text" },
  { id: "2", name: "announcements", type: "text" },
  { id: "3", name: "voice-chat", type: "voice" },
  { id: "4", name: "gaming", type: "voice" },
]

export function ChannelSidebar() {
  const { currentServer, currentChannel, setCurrentChannel } = useServer()

  return (
    <div className="fixed left-[72px] flex h-screen w-60 flex-col bg-[#2B2D31]">
      {/* Server Header */}
      <div className="flex h-12 items-center justify-between border-b border-[#1E1F22] px-4 py-3">
        <h2 className="font-semibold text-white truncate">{currentServer?.name || "Select a Server"}</h2>
        <Button variant="ghost" size="icon" className="h-6 w-6 text-zinc-400 hover:text-white">
          <ChevronDown className="h-4 w-4" />
        </Button>
      </div>

      {/* Channel List */}
      <ScrollArea className="flex-1 px-2">
        <div className="mt-4 space-y-4">
          {/* Text Channels */}
          <div>
            <div className="flex items-center justify-between px-2 mb-1">
              <span className="text-xs font-semibold uppercase text-zinc-400">
                Text Channels
              </span>
              <Button
                variant="ghost"
                size="icon"
                className="h-6 w-6 text-zinc-400 hover:text-white"
              >
                <PlusCircle className="h-4 w-4" />
              </Button>
            </div>
            <div className="space-y-[2px]">
              {mockChannels
                .filter((channel) => channel.type === "text")
                .map((channel) => (
                  <Button
                    key={channel.id}
                    variant="ghost"
                    className={`w-full justify-start px-2 py-1 hover:text-white hover:bg-[#35373C] ${
                      currentChannel?.id === channel.id ? "bg-[#35373C] text-white" : "text-zinc-400"
                    }`}
                    onClick={() => setCurrentChannel(channel)}
                  >
                    <Hash className="mr-2 h-4 w-4" />
                    <span className="truncate">{channel.name}</span>
                  </Button>
                ))}
            </div>
          </div>

          {/* Voice Channels */}
          <div>
            <div className="flex items-center justify-between px-2 mb-1">
              <span className="text-xs font-semibold uppercase text-zinc-400">
                Voice Channels
              </span>
              <Button
                variant="ghost"
                size="icon"
                className="h-6 w-6 text-zinc-400 hover:text-white"
              >
                <PlusCircle className="h-4 w-4" />
              </Button>
            </div>
            <div className="space-y-[2px]">
              {mockChannels
                .filter((channel) => channel.type === "voice")
                .map((channel) => (
                  <Button
                    key={channel.id}
                    variant="ghost"
                    className={`w-full justify-start px-2 py-1 hover:text-white hover:bg-[#35373C] ${
                      currentChannel?.id === channel.id ? "bg-[#35373C] text-white" : "text-zinc-400"
                    }`}
                    onClick={() => setCurrentChannel(channel)}
                  >
                    <Volume2 className="mr-2 h-4 w-4" />
                    <span className="truncate">{channel.name}</span>
                  </Button>
                ))}
            </div>
          </div>
        </div>
      </ScrollArea>

      {/* User Section */}
      <div className="flex h-[52px] items-center gap-2 bg-[#232428] px-2">
        <UserMenu />
      </div>
    </div>
  )
}
