"use client"

import { createContext, useContext, useState, useCallback } from "react"

interface Server {
  id: string
  name: string
  imageUrl: string
}

interface Channel {
  id: string
  name: string
  type: "text" | "voice"
}

interface ServerContextType {
  servers: Server[]
  currentServer: Server | null
  currentChannel: Channel | null
  setCurrentServer: (server: Server) => void
  setCurrentChannel: (channel: Channel) => void
}

const ServerContext = createContext<ServerContextType | undefined>(undefined)

// Mock data - will be replaced with real data from API
const mockServers: Server[] = [
  { id: "1", name: "Gaming Hub", imageUrl: "https://via.placeholder.com/48" },
  { id: "2", name: "Study Group", imageUrl: "https://via.placeholder.com/48" },
  { id: "3", name: "Movie Club", imageUrl: "https://via.placeholder.com/48" },
]

export function ServerProvider({ children }: { children: React.ReactNode }) {
  const [currentServer, setCurrentServer] = useState<Server | null>(mockServers[0])
  const [currentChannel, setCurrentChannel] = useState<Channel | null>(null)

  const handleSetCurrentServer = useCallback((server: Server) => {
    setCurrentServer(server)
    setCurrentChannel(null) // Reset channel when switching servers
  }, [])

  const value = {
    servers: mockServers,
    currentServer,
    currentChannel,
    setCurrentServer: handleSetCurrentServer,
    setCurrentChannel,
  }

  return (
    <ServerContext.Provider value={value}>
      {children}
    </ServerContext.Provider>
  )
}

export function useServer() {
  const context = useContext(ServerContext)
  if (context === undefined) {
    throw new Error("useServer must be used within a ServerProvider")
  }
  return context
}
